@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;700);
.ManualInput .inputs{display:grid;grid-template-columns:1fr 1fr;margin:18px 0;grid-column-gap:18px;-webkit-column-gap:18px;column-gap:18px}.ManualInput .action{margin-bottom:18px;text-align:center}.ManualInput .action .compare{display:inline-block;margin:auto;padding:6px 12px;border:1px solid #fff;background-color:#282c34;color:#fff;cursor:pointer}.SingleInput textarea{width:100%;height:400px;font-family:Inconsolata,monospace}
.DisplayError{margin:18px 0}.DisplayError pre{color:#ef476f}.ComparedValue.left{background-color:#bf113a}.ComparedValue.right{background-color:#037356}.ComparedValue .mixed{background-color:#f4a261}.ComparedValue .collapsable{cursor:pointer}.ComparedValue .collapsable:hover{background-color:#3e4451}.ComparedValue .collapsable.mixed:hover{background-color:#f7be91}.ComparedValues pre{margin:0;padding:3px}
.App{margin:18px}.App h1{margin:18px;text-align:center}
*,::before,::after{box-sizing:border-box}body{margin:0;background-color:#282c34;color:#fff;font-family:Roboto,Arial,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}
